import xhr from './xhr/';

/**
 * 账号模块 API
 */
class AccountService {
  /**
   * 登录
   * @param  {String} formData.userName
   * @param  {String} formData.password
   */
  login (formData) {
    return xhr({
      url: '/auth/user/login',
      body: formData,
      onlyOne: true,
    });
  }

  /**
   * 注销登录
   */
  logout () {
    return xhr({
      url: '/auth/user/logout',
    });
  }

  /**
   * 获取登录信息
   */
  getLoginInfo () {
    return xhr({
      url: '/auth/user/getLoginInfo',
    });
  }

  /**
   * 验证码
   */
  getVcode () {
    return xhr({
      url: '/auth/user/getVerifyCode',
    });
  }
}

// 实例化后导出，全局单例
export default new AccountService();
