import * as types from '../types/account-types';
import accountService from '@/services/account-service';
import authConfig from '@/store/authConfig.json';

// initial state
const state = {};

// getters
const getters = {};

const arrayEqual = (arr1, arr2) => {
  if (arr1.length !== arr2.length) return false;
  for (var i = 0; i < arr1.length; ++i) {
    if (arr1[i] !== arr2[i]) return false;
  }
  return true;
};

// actions
const actions = {
  [types.LOGIN_REQUEST] (info, formData) {
    return accountService.login(formData);
  },

  [types.LOGOUT_REQUEST] () {
    return accountService.logout();
  },

  [types.GET_LOGIN_INFO_REQUEST] ({
    commit,
    state,
  }) {
    return accountService
      .getLoginInfo()
      .then(data => {
        const oldAuthInfo = window.localStorage.authInfo; // 读取本地authInfo
        if (!oldAuthInfo || !arrayEqual(JSON.parse(window.localStorage.authList || '[]'), data)) {
          const authInfo = {};
          Object.keys(authConfig).forEach(key => {
            const authArr = authConfig[key];

            if (authArr === 'authIgnore') {
              authInfo[key] = true;
            } else {
              if (authArr[0] === 'and') {
                if (authArr.slice(1).every(auth => data.indexOf(auth) >= 0)) {
                  authInfo[key] = true;
                }
              } else if (authArr.some(auth => data.indexOf(auth) >= 0)) {
                authInfo[key] = true;
              }
            }
          });
          window.localStorage.setItem('authInfo', JSON.stringify(authInfo));
        }
        window.localStorage.setItem('authList', JSON.stringify(data || '[]'));
        return data;
      });
  },

  [types.GET_VCODE_REQUEST] () {
    return accountService.getVcode();
  },
};

// mutations
const mutations = {};

export default {
  state,
  getters,
  actions,
  mutations,
};
